.contact-section {
    color: #396f8c;
    font-family: "GothamPro";
    font-size: 14px;
    font-weight: 400;
    background-color: #dee6eb;
    padding: 35px 20px;
    &:nth-child(even) {
        background-color: transparent; }
    &__title {
        margin-bottom: 30px;
        @include md {
            font-size: 20px; } } }
.contact-block {
    display: flex;
    justify-content: space-between;
    @include md {
        flex-direction: column; }
    &__title {
        padding-bottom: 15px; }
    &__item {
        max-width: 355px;
        @include md {
            margin-bottom: 25px;
            &:last-child {
                margin-bottom: 0; } }
        & span {
            margin-right: 10px; } } }
