.partners {
    background-color: #eaeff2;
    padding: 0 20px;
    padding-bottom: 50px;
    &__title {
        margin-bottom: 32px; }
    &__row {
        display: flex;
        flex-wrap: wrap;
        justify-content: center; }
    &__item {
        &:first-child {
            margin-bottom: 12px; } }
    &__icon {
        width: 224px;
        height: 101px;
        position: relative;
        margin: 0 auto;
        cursor: pointer;
        &::after {
            content: "";
            background-image: url(/img/partners-sprite1.png);
            position: absolute; } }
    &__icon_sberbank {
        &::after {
            width: 179px;
            height: 39px;
            background-position: -5px -5px;
            top: 50%;
            transform: translateY(-50%);
            left: 0;
            right: 0;
            margin: 0 auto; }
        &:hover::after {
            background-position: -194px -5px; } }
