.video-wrap {
    position: relative;
    z-index: 1;
    @include md {
        display: none; } }

.modal {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 1042px;
    height: 587px;
    max-width: 100%;
    max-height: 100%;
    z-index: 1010;
    display: none;
    &__guts {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%; }
    &__close-btn {
        position: absolute;
        width: 63px;
        height: 63px;
        z-index: 1;
        top: 10px;
        top: 0px;
        right: -110px;
        display: block;
        border-radius: 50%;
        background-color: #f58220;
        &:before {
            content: url(../img/close.svg);
            position: absolute;
            width: 22px;
            transform: translate(-50%, -50%);
            top: 50%;
            left: 50%; }
        &:hover {
            background-color: #405b71; } }
    &__video {
        position: relative; }

    &__btn {
        content: url(../img/play-btn.png);
        color: #fff;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        cursor: pointer;
        z-index: 4;
        @include sm {
            width: 60px;
            height: 60px; } }
    &__item::-webkit-media-controls-panel {
        display: none; }
    &__item {
        width: 100%;
        height: 100%;
        border-radius: 8px;
        position: relative;
        z-index: 3; } }

// .closed
