.range {
    display: flex;
    // align-items: center
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 20px;
    z-index: 1;
    &__input {
        width: 101px; }
    &__text {
        padding: 0 0 0 20px;
        font-family: 'GothamPro';
        color: #396f8c;
        font-size: 12px;
        font-weight: 600;
        letter-spacing: 0.48px;
        &:first-child {
            padding-right: 30px;
            padding-left: 0; } } }
.noUi-connects {
    border-top: 2px dotted #396f8c;
    border-radius: 0px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -10%); }
.noUi-handle {
    border: none;
    border-radius: 0;
    background: none;
    cursor: default;
    box-shadow: none; }

.noUi-handle-lower .noUi-touch-area {
    display: none; }
.noUi-touch-area {
    height: 4px;
    width: 4px;
    background: #396f8c;
    border-radius: 50%;
    position: fixed;
    top: 0%;
    left: 0%;
    transform: translate(-65%,140%); }

.noUi-connect {
    background: none; }
.noUi-target {
    background: none;
    box-shadow: none;
    border-radius: 0px;
    cursor: pointer; }

.noUi-handle-lower:before {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #f0f3f6;
    width: 38px;
    height: 38px;
    border: 1px solid #396f8c;
    border-radius: 50%;
    cursor: pointer;
    z-index: 1; }

.noUi-handle-lower:after {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 4px;
    height: 4px;
    background: #396f8c;
    border-radius: 50%;
    cursor: pointer;
    z-index: 1; }

[disabled] .noUi-handle {
    pointer-events: none;
    cursor: pointer; }
.noUi-handle-upper:before {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #f0f3f6;
    width: 14px;
    height: 14px;
    border: 1px solid #396f8c;
    border-radius: 50%;
    cursor: pointer;
    z-index: 1; }

.noUi-handle-upper:after {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 4px;
    height: 4px;
    background: #396f8c;
    border-radius: 50%;
    cursor: pointer;
    z-index: 1; }
