.contact {
    &__title {
        color: #2e5b72;
        font-size: 16px;
        font-weight: 500;
        line-height: 22px; }
    &__info {
        display: flex;
        flex-direction: column;
        color: #396f8c;
        font-size: 14px;
        font-weight: 400;
        @include lg {
            padding: 0 20px; }
        &:first-child {
            margin-top: 65px;
            @include md {
                margin-top: 0; } }
        & div:last-child {
            margin-right: 0; } }
    &__item {
        display: flex;
        align-items: center;
        padding-bottom: 22px;
        &:last-child {
            padding-bottom: 40px; }
        & div {
            margin-right: 30px;
            @include md {
                margin-right: 0; } }
        @include md {
            flex-direction: column;
            align-items: flex-start; }
        & div {
            @include md {
                margin-bottom: 10px; }
            &:last-child {
                margin-bottom: 0; } } } }

