.header-wrapper {
    width: 100%;
    margin-bottom: 20px;
    @include md {
        margin-bottom: 15px; } }

.header {
    font-family: 'GothamPro';
    height: 10vh;
    display: flex;
    flex-direction: column;
    padding: 0 20px;
    padding-top: 22px;
    position: relative;
    z-index: 1;
    @include md {
        padding-top: 10px;
        margin-bottom: 0;
        height: auto; }
    &__inner {
        display: flex;
        width: 100%;
        flex-direction: column; }
    &__right {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        margin-bottom: 10px;
        margin-top: 5px;
        @include md {
            display: none; } }
    &__right > div {
        margin-right: 40px; }
    &__right >div:last-child {
        margin-right: 0; }
    &_absolute {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        margin: 0;
        padding-left: 20px;
        padding-right: 20px;
        @include md {
            position: static; } } }

.logo {
    &__img_md {
        display: none;
        @include md {
            display: block; } }
    &__img_lg {
        @include md {
            display: none; } } }

.find-block {
    display: flex;
    position: relative;
    &__input {
        width: 250px;
        padding: 7px 27px 7px 4px;
        background-color: #cadae3;
        position: absolute;
        left: -251px;
        top: -13px; }
    &__arrow {
        background-image: url(/img/find.png);
        background-repeat: no-repeat;
        display: block;
        cursor: pointer; }
    &_sprite {
        width: 17px;
        height: 17px;
        background-position: -5px -5px;
        position: absolute;
        width: 17px;
        height: 17px;
        right: 7px;
        top: 50%;
        transform: translateY(-50%);
        &:hover {
            background-position: -32px -5px; } } }

.signIn-link {
    opacity: 0.8;
    color: #396f8c;
    font-size: 14px;
    font-weight: 400;
    letter-spacing: 0.7px;
    cursor: pointer;
    transition: 0.3s;
    &:hover {
        color: #2e5b72;
        opacity: 1; } }

.lang {
    position: relative;
    color: #396f8c;
    font-size: 14px;
    font-weight: 400;
    letter-spacing: 0.28px;
    &__btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 50px;
        cursor: pointer;
        &:hover .lang__arrow {
            border-top-color: #f58220; } }
    &__current {
        border-bottom: 1px dotted #9ab5c6;
        display: block;
        line-height: 12px;
        cursor: pointer; }
    &__arrow {
        width: 0;
        height: 0;
        border: 5px solid transparent;
        border-top-color: #396f8c;
        opacity: 0.7;
        border-bottom: 0;
        transition: 0.1s;
        margin-left: 2px; }
    &__list {
        position: absolute;
        top: 100%;
        left: 0;
        display: none;
        text-align: center;
        width: 40px;
        flex-direction: column;
        background-color: #eaeff2;
        transition: 0.5s; }
    &__item {
        padding: 5px;
        font-size: 14px; }
    &__link {
        text-decoration: none;
        color: #396f8c;
        &:hover {
            color: #f58220; } } }

.nav {
    &__menu {
        display: flex;
        justify-content: flex-end;
        margin-left: 70px;
        @include md {
            margin-left: 50px; } }
    &__link {
        color: #396f8c;
        font-size: 14px;
        font-weight: 500;
        text-transform: uppercase;
        letter-spacing: 0.56px;
        text-decoration: none;
        transition: 0.3s;
        margin-right: 38px;
        @include md {
            font-size: 13px;
            margin-right: 30px;
            margin-bottom: 15px; }
        &:last-child {
            margin-right: 0;
            @include md {
                margin-bottom: 0; } }
        &:hover {
            color: #f58220; } }
    &__icon {
        display: none;
        width: 33px;
        height: 30px;
        position: relative;
        cursor: pointer;
        transform: translateY(20%);
        @include md {
            display: inline-block; } }
    &__item {
        @include md {
            position: absolute;
            display: none;
            top: 0;
            right: 0;
            left: 0;
            margin-top: 52px;
            padding: 12px;
            background-color: #ffffff;
            z-index: 1000;
            overflow: auto; } }
    &__icon span {
        display: block;
        position: absolute;
        height: 3px;
        width: 100%;
        background-color: #396f8c;
        opacity: 1;
        left: 0;
        transform: rotate(0deg);
        transition: 0.4s; }
    &__icon span:nth-child(1) {
        top: 0px; }
    &__icon span:nth-child(2) {
        top: 13px; }
    &__icon span:nth-child(3) {
        top: 13px; }
    &__icon span:nth-child(4) {
        top: 26px; }
    &__menu.menu_stage_open .nav__icon span:nth-child(1) {
        top: 18px;
        width: 0%;
        left: 50%; }
    &__menu.menu_stage_open .nav__icon span:nth-child(2) {
        transform: rotate(45deg); }
    &__menu.menu_stage_open .nav__icon span:nth-child(3) {
        transform: rotate(-45deg); }
    &__menu.menu_stage_open .nav__icon span:nth-child(4) {
        top: 18px;
        width: 0%;
        left: 50%; }
    &__menu.menu_stage_open .nav__item {
        @include md {
            display: block; } }
    &__menu.menu_stage_open .nav__link {
        @include md {
            display: block; } }
    &__link {
        @include md {
            display: none; } } }
