.modalWindow {
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1;
    overflow: auto;
    background-color: rgba(231, 237, 240, 0.9);
    display: none;
    @include md {
        background-color: rgba(231, 237, 240, 1);
        height: 100%; } }

.container {
    margin: 25vh 25vw;
    width: 50vw;
    height: 50vh;
    box-sizing: border-box;
    padding: 25px 15px;
    position: relative;
    @include md {
        margin: 0vh 0vw;
        width: 100%;
        height: auto; } }

.modalContent {
    display: none; }

.content-modal[data-items] .img-modal {
    @include md {
        background-image: none;
        order: -1; } }

.img-modal img {
    display: none;
    @include md {
        display: block;
        margin: 0 auto;
        max-width: 180px; } }
.content-modal[data-items="1"] {
    .img-modal {
        background-image: url(../img/img4.png); } }

.content-modal[data-items="2"] {
    .img-modal {
        background-image: url(../img/img3.png); } }

.content-modal[data-items="3"] {
    color: #dddddd; }

.content-modal[data-items="4"] {
    .img-modal {
        background-image: url(../img/img1.png); } }

.content-modal[data-items="5"] {
    color: yellow; }

.content-modal[data-items="6"] {
    color: grey; }


.img-modal {
    background-image: url(../img/img2.png);
    height: 420px;
    background-position: center;
    background-size: cover;
    position: relative;
    z-index: -1;
    @include md {
        height: auto; } }

.img-modal:before {
    content: url(../img/small-border-line-top.png);
    transform: translate(-36%,-49%);
    position: absolute;
    top: 50%;
    left: 50%;
    transition: .5s;
    z-index: -1; }

.img-modal:after {
    content: url(../img/small-border-line-bottom.png);
    transform: translate(-58%,53%) rotate(5deg);
    position: absolute;
    top: 50%;
    left: 50%;
    transition: .5s;
    z-index: -1; }

.slide-system-integration-hero {
    width: 420px;
    height: 420px;
    border-radius: 50%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    @include md {
        display: flex;
        flex-direction: column;
        position: static;
        width: auto;
        height: auto;
        transform: translate(0); }

    &__desc {
        width: 326px;
        text-align: center;
        color: #ffffff;
        font-family: 'GothamPro';
        font-size: 16px;
        font-weight: 400;
        line-height: 21px;
        margin-bottom: 34px;
        padding: 5px;
        @include md {
            width: 100%;
            color: #396f8c;
            line-height: 27px;
            text-align: left;
            margin: 0;
            padding: 0; } } }

.pop-up-container {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(78, 100, 127, 0.7);
    border-radius: 50%;
    @include md {
        background-color: transparent;
        position: static; } }

.slide-system-integration-hero__text {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 326px;
    @include md {
        top: 0%;
        left: 0%;
        transform: translate(0%);
        width: 100%; } }

.slide-system-integration-hero__btn {
    border-radius: 14px;
    border: 1px solid #ffffff;
    background-color: transparent;
    padding: 6px 23px;
    color: #ffffff;
    font-size: 14px;
    font-weight: 400;
    letter-spacing: 0.7px;
    display: block;
    margin: 0 auto;
    cursor: pointer;
    transition: 0.3s;
    @include md {
        margin: 28px 0 37px;
        border: 1px solid #2c5b72;
        color: #2c5b72; } }

.slide-system-integration-hero__btn:hover {
    color: #396f8c;
    background-color: #ffffff;
    @include md {
        background-color: #2c5b72;
        color: #ffffff; } }

.close-button-modal {
    position: absolute;
    top: -60px;
    right: 0px;

    @include md {
        width: 52px;
        height: 52px;
        top: -14px;
        transform: translate(267%, 0%);
        right: 50%; } }

.close-button-modal img {
    display: block;
    width: 19px;
    height: 19px;
    @include md {
        width: 16px;
        height: 16px; } }

.close-button-modal:hover {
    background-color: #2c5b72; }

