.hero {
  min-height: 80vh;
  overflow: hidden;
  position: relative;
}

.hero-wrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media only screen and (max-width: 1023px) {
  .hero-wrapper {
    position: static;
    top: 0;
    left: 0;
    transform: translate(0%, 0%);
  }
}

.slider {
  opacity: 0;
  visibility: hidden;
  max-width: 460px;
  margin: 0 auto;
}

.slider.owl-loaded {
  opacity: 1;
  visibility: visible;
}

.slider .owl-stage-outer {
  overflow: visible;
}

.slider__wrapper {
  position: relative;
  width: 560px;
  padding: 0 90px;
}

@media only screen and (max-width: 1023px) {
  .slider__wrapper {
    margin-bottom: 30px;
    padding: 0 20px;
  }
}

.slider__item {
  transition: 0.9s;
  cursor: pointer;
}

.slider__item:hover .slider__title {
  color: #f58220;
}

.slider__img {
  position: relative;
  transition: 0.5s;
  cursor: pointer;
}

.slider__img img {
  border: 2px solid #53788b;
  padding: 10px;
  box-sizing: border-box;
  border-radius: 50%;
  transition: 0.5s;
  cursor: pointer;
}

@media only screen and (max-width: 1023px) {
  .slider__img img {
    max-width: 100%;
    margin: auto;
    height: auto;
    width: auto;
    padding: 5px;
  }
}

.slider__img img:hover {
  border: 2px solid transparent;
  box-sizing: border-box;
}

.slider__img:before {
  content: url(../img/border-line-top.svg);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-41%, -47%);
  visibility: hidden;
  opacity: 0;
  transition: 0.5s;
}

.slider__img:after {
  content: url(../img/border-line-bottom.svg);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-54%, 45%);
  visibility: hidden;
  transition: 0.5s;
  opacity: 0;
}

.slider__img:hover:before {
  content: url(../img/border-line-top.svg);
  visibility: visible;
  opacity: 1;
  transition: 0.5s;
}

@media only screen and (max-width: 1023px) {
  .slider__img:hover:before {
    display: none;
  }
}

.slider__img:hover:after {
  content: url(../img/border-line-bottom.svg);
  visibility: visible;
  transition: 0.5s;
  opacity: 1;
}

@media only screen and (max-width: 1023px) {
  .slider__img:hover:after {
    display: none;
  }
}

.slider__title {
  padding-top: 45px;
  text-align: center;
  color: #396f8c;
  font-size: 20px;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.8px;
  transition: 0.5s;
}

@media only screen and (max-width: 1023px) {
  .slider__title {
    font-size: 11px;
    letter-spacing: 0.56px;
    padding-top: 15px;
    max-width: 100px;
    margin: 0 auto;
  }
}

.blur {
  box-sizing: border-box;
  filter: blur(2px);
  transition: 0.9s;
}

@media only screen and (max-width: 1023px) {
  .blur {
    filter: blur(0);
  }
}

